import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

const DivBox = styled.div`
  padding : 20px;
`;

const ItemTitle = styled.h2`
  font-size : 25px;
  color : grey;
`;

function Detail(props) {

  let [alertModal, alertModalChange] = useState(true);
  // let [inputData, inputDataChange] = useState('');

  useEffect(() => {
    let timer = setTimeout(() => { alertModalChange(false) }, 2000)
    return () => { clearTimeout(timer) }; // To prevent unnecessary errors
  }, [alertModal]);

  let { id } = useParams();
  let history = useHistory();
  // let foundItem = props.shoes.find(x => x.id == id)
  let foundItem = props.shoes.find(function (item) {
    return item.id == id
  });

  return (
    <div className="container">
      <DivBox>
        <ItemTitle className="grey">Item Detail Page</ItemTitle>
      </DivBox>

      {/* <input onChange={(e) => { inputDataChange(e.target.value) }} />
      {inputData} */}
      {
        alertModal === true
          ? <div className="my-alert-yellow">
            <p>Only few left!</p>
          </div>
          : null
      }

      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{foundItem.title}</h4>
          <p>{foundItem.content}</p>
          <p>{foundItem.price}</p>

          <Info stock={props.stock}></Info>

          <button className="btn btn-danger" onClick={() => { props.stockChange(props.stock - 1) }}>Order</button>
          <button className="btn btn-danger" onClick={() => {
            history.goBack();
          }}>Go Back</button>
        </div>
      </div>
    </div >
  );
};

function Info(props) {
  return (
    <p>Stock: {props.stock}</p>
  )
}

export default Detail