import React, { useState } from 'react';
import './App.css';
import { Navbar, Container, Nav, NavDropdown, Button } from 'react-bootstrap';
import productData from './data';
import Detail from './Detail';
import axios from 'axios';

import { Link, Route } from 'react-router-dom';

function App() {

  let [shoes, shoesChange] = useState(productData);
  let [stock, stockChange] = useState(10);

  return (
    <div className="App">

      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">Shoe Shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Route exact path="/">
        <div className="main-banner">
          <h1>20% Season Off</h1>
          <p>
            This is a simple hero unit.
          </p>
          <p>
            <Button variant="primary">Go To Shop</Button>{' '}
          </p>
        </div>

        <div className="container">
          <div className="row">
            {
              shoes.map(function (item, i) {
                return (
                  <Item shoes={item} i={i} key={i} />
                )
              })
            }
          </div>
          <button className="btn btn-primary" onClick={() => {

            //스피너 띄우기
            // axios.post('서버URL', { 전달할데이터 })

            axios.get('https://codingapple1.github.io/shop/data2.json')
              .then((result) => {
                //로딩 중인 것 없애기
                shoesChange([...shoes, ...result.data]) // Add data in array shoes
              }) // if sucess
              .catch(() => {
                console.log('실패')
              }) // if fail
          }}>More</button>
        </div>
      </Route>

      <Route path="/detail/:id">
        <Detail shoes={shoes} stock={stock} stockChange={stockChange} />
      </Route>
    </div>
  );
};

function Item(props) {
  return (
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + (props.i + 1) + '.jpg'} width="100%" />
      <h3>{props.shoes.price}</h3>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
    </div>
  );
};

export default App;